*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #7a7a7a;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 414px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding: 0 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 85px;
  }
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

.visual-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: 1px;
  padding: 0;
  list-style: none;
  position: absolute;
  overflow: hidden;
}

